@media (max-width: 767px) {
  .nav {
    padding: 22px 0 0;
    box-shadow: 0 1px 0 0 #ecececec, 0 1px 0 0 #ecececec inset;
  }

  .nav--closed .nav__button {
    position: absolute;
    top: 32px;
    right: 30px;
  }

  .nav--opened {
    position: absolute;
    background-color: #ffffff;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    min-width: 320px;
  }

  .nav--opened .nav__button {
    position: absolute;
    top: 23px;
    right: 27px;
  }

  .nav__logo-wrapper {
    position: relative;
    padding-bottom: 24px;
  }
  .nav__logo-wrapper--additional::before {
    background-color: #f1f1f1;
  }

  .nav__link {
    width: 86px;
    height: 35px;
    display: block;
    margin: 0 auto;
  }

  .nav--closed .toggle {
    cursor: pointer;
    display: block;
    width: 21px;
    height: 15px;
    z-index: 2;
  }
  .nav--closed .toggle:hover::before {
    opacity: 0.6;
  }
  .nav--closed .toggle:active::before {
    opacity: 0.3;
  }
  .nav--closed .toggle::before {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: #231f20;
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
  }
  .nav--closed .toggle:focus-visible, .nav--closed .toggle:focus {
    outline-offset: 5px;
  }

  .nav--opened .toggle {
    display: block;
    width: 27px;
    height: 29px;
    z-index: 2;
  }
  .nav--opened .toggle::before, .nav--opened .toggle::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: #231f20;
    position: absolute;
    top: 50%;
    left: 0;
  }
  .nav--opened .toggle:hover::before, .nav--opened .toggle:hover::after, .nav--opened .toggle:focus::before, .nav--opened .toggle:focus::after {
    background-color: rgba(0, 0, 0, 0.6);
  }
  .nav--opened .toggle:active::before, .nav--opened .toggle:active::after {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .nav--opened .toggle:focus-visible, .nav--opened .toggle:focus {
    outline-offset: 5px;
  }
  .nav--opened .toggle::before {
    transform: rotate(45deg);
  }
  .nav--opened .toggle::after {
    transform: rotate(-45deg);
  }
}
@media (min-width: 768px) {
