  }
  .nav__logo-wrapper::before {
    content: "";
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    width: 100vw;
    height: 500px;
    left: -32px;
    position: absolute;
    background-color: #f9f9f9;
    bottom: -501px;
  }
  .nav__logo-wrapper--additional::before {
    background-color: #f1f1f1;
  }
}
@media (min-width: 1150px) {
  .nav {
    grid-template-columns: 204px 204px 312px 1fr 1fr;
  }

  .nav__link {
    width: 149px;
    height: 72px;
    left: calc(50% - 95px);
    top: 8px;
  }

  .nav__logo-wrapper {
    grid-column: 3/4;
  }
  .nav__logo-wrapper::before {
    content: "";
    width: 100vw;
    height: 500px;
    background-color: #f9f9f9;
    position: absolute;
    left: calc(100% - 156px);
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    top: 100%;
    z-index: -1;
  }
  .nav__logo-wrapper--additional::before {
