  }
  .nav__logo-wrapper::after {
    content: "";
    width: 100vw;
    height: 600px;
    background-color: #f9f9f9;
    position: absolute;
    right: calc(100% - 156px);
    border-top: 1px solid rgba(0, 0, 0, 0.1);
